import test from 'ava'
import allergiescount from './index'

test('Eggs and peanuts', t => {
  let expected = 3
  t.deepEqual(allergiescount(['eggs', 'peanuts']), expected)
})

test('Tomatoes, Strawberries, eggs, shellfish', t => {
  let expected = 29
  t.deepEqual(allergiescount(['eggs','tomatoes','strawberries','shellfish']), expected)
})

test('Tomatoes', t => {
  let expected = 16
  t.deepEqual(allergiescount(['tomatoes'], expected))
})